.Input{
	width: 100%;
	padding: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.Label{
	font-weight: bold;
	display: block;
	margin-bottom:8px;
}
.InputElement{
	outline:none;
	border:1px solid #ccc;
	background-color:white;
	font:inherit;
	padding:6px 10px;
	display: block;
	width:100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.InputElement:focus{
	outline:none;
	background-color:#ccc;
}


